export default {
	data() {
		return {
			detail_res: {
				name: '全自动热压机',
				time: '2019-09-30',
				schedule: '40%',
				hour: 210,
				cost: 40000,
				err: 20,
				address: '深圳市龙华区大浪行政中心浪心科技园F栋301',
				member: 7
			},
			r_item:{}
		};
	},
	onLoad: function (option) {
		
	    this.r_item = JSON.parse(decodeURIComponent(option.item));
	},
	computed:{
		validityPeriod(){
			return this.$utils.format_date(this._get('validityPeriod',''))
		},
		workAddress(){
			let address = JSON.parse(this._get('workAddress',''))
			if(Array.isArray(address)){
				return address.join()
			}else{
				return address
			}
			
		},
		item_tag(){
			try{
				let welfareInfo = JSON.parse(this._get('welfareInfo',[]))
				let workRequest = JSON.parse(this._get('workRequest',[]))
				return welfareInfo.concat(workRequest)
			}catch(e){
				//TODO handle the exception
			}
		},
		requirementInfo(){
			let r_info= []
			try{
				 r_info= JSON.parse(this._get('requirementInfo',[]))
			}catch(e){
				//TODO handle the exception
			}
			return r_info
		},
		
	},
	methods: {
		_get(key,defaultValue){
			return this.$utils._get(this.r_item,key,defaultValue)
		},
		goto_work(){
			this.$store.commit('setArtisanWorkState','waitStart')
			uni.switchTab({
				url:'../../workbench/workbench',
			})
		},
		go_back(){
			uni.navigateBack({
			    delta: 1
			});
		},
		submitWork(e){
			console.log(e)
			this.$http.post('personwx.recpersonchangestatus/1.0/',{
				id:this._get('rpId',''),
				status: '2',
				formId:e.detail.formId
			}).then(res =>{
				console.log(res)
				if(res.data.code == '0'){
					uni.showToast({
					    title: '接工成功',
					    duration: 2000,
						success:() =>{
							this.go_back()
						}
					});
					
				}
			})
		},
		refusedWork(e){
			console.log('sds',e)
			this.$http.post('personwx.recpersonchangestatus/1.0/',{
				id:this._get('rpId',''),
				status: '3',
				formId:e.detail.formId
			}).then(res =>{
				console.log(res)
				if(res.data.code == '0'){
					uni.showToast({
					    title: '已拒绝',
					    duration: 2000,
						success:() =>{
							this.go_back()
						}
					});
					
				}
			})
		},
		comma(index){
			return index<this.requirementInfo.length-1?'，':''
		}
	}
};
